@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap');

*{
  padding: 0;
  margin: 0;
  font-family: "jast",sans-serif; 
}

body{
  background: #f3f2ef;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  font-family:  "jast",sans-serif;
}

.wave{
  top: 115%;
}

.inputData{
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  text-align: center;
}

html{
  font-size: 100%;
  height: 100%;
}

html{
  background: #eee;

}

.box{
  
  width: 25vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0,0,0 0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transform: translate3d(0,0,0);
  min-width: 20rem;
  min-height: 35rem;
  
}

.inputField{
width: 50%;
margin: auto;
border-radius: 5rem;
padding: 1rem 1rem;
outline: none;
font-size: 1.5 rem;
}

.errorMsg{
text-align: center;
font-size: 2rem;
margin: 2rem auto;
}

@media(max-width:600px){
  .box{
    width: 90vw;
    height: 80vh;
}
.wave{
  top: 85%;
}
.weathercon{
  font-size: 1.5rem;
}
}

@media(max-height:500px){
  .box{
    height: 80vh;
  }
  .wave{
    top: 115%;
  }
}

body>span{
  width: 100vw;
  text-align: center;
  color: gray;
}

#weathercon{
  height: 55%;
  display: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

#weathercon.fas{
  font-size: 6rem;
  animation: fas-anime3s linear infinite alternate;
}

@keyframes fas-anime{
  from{
    transform: scale(1.1);
  }
  to{
    transform: scale(1.5);
  }
}

@keyframes rotates{
  from{
transform: translateX(-0.5rem) ;
  }
  to{
    transform: translate(0.5rem);
  }
}

@keyframes drift{
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
.info{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 60vh;
  z-index: 4;
}

#date{
  text-align: center;
  margin-top: 0.5rem;
  color: #57606f;
  font-size: 1.2rem;
  font-weight: 300;
  text-transform: uppercase;
}

.location{
  margin-top: 5.5rem;
  text-align: center;
  font-size:3rem;
  text-transform: capitalize;
}

.temp{
  margin-top: 2.5rem;
  text-align: center;
  font-size: 3rem;

}

.tempin-max{
  text-align: center;
  margin-top: 0.3rem;
  font-size: .8rem;
  color: #57606f;
}

.wave-one{
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.wave-three{
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 5000ms infinite linear;
  opacity: 0.2;
  z-index: 2 !important;
}

.wave-two{
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 7000ms infinite linear ;
  opacity: 0.1;
  z-index: 3 !important;
}

.box:after{
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11%;
  transform: translate3d(0,0,0);
}